import { AfterViewChecked, AfterViewInit, Component, Input, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { cpuUsage } from 'process';
import 'hammerjs'
import { HammerModule } from '@angular/platform-browser';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.sass']
})
export class AppComponent implements OnInit, AfterViewInit {

  title = 'ihm-scrollbar';

  public scrollProgress = 0;
  private pageContent: HTMLElement;
  public currentPageNumber = 1;
  public totalPageNumber = 11;

  constructor(
    private route: ActivatedRoute
  ) {}

  ngOnInit(): void {
    console.log(this.route.snapshot.params)
    //this.currentPageNumber = +this.route.snapshot.paramMap.get("page");
  }

  ngAfterViewInit(): void {
    this.pageContent= document.getElementById('page-content');
  }

  public setScrollProgress() {
    let height = this.pageContent.clientHeight;
    let scrollHeight = this.pageContent.scrollHeight - height;
    let scrollTop = this.pageContent.scrollTop;
    this.scrollProgress = Math.floor(scrollTop / scrollHeight * 100);
  }

  public onSwipeLeft() {
    console.log("swipe left")
    if(this.currentPageNumber >= this.totalPageNumber) {
      return
    }
    this.currentPageNumber ++;
    this.changePage();
  }

  public onSwipeRight() {
    console.log("swipe right")
    if(this.currentPageNumber <= 1) {
      return
    }
    this.currentPageNumber --;
    this.changePage();
  }

  public changePage() {
    this.pageContent.scrollTo(0,0)
  }


}
